const STORAGE_KEY = 'todos-vue.js'

export default{
  fech(){
    return JSON.parse(window.sessionStorage.getItem(STORAGE_KEY))||[]
  },
  save(items){
    window.sessionStorage.setItem(STORAGE_KEY,JSON.stringify(items))
  }
}



