<template>
  <div id="app">
    <h1 v-text="title"></h1>
    <input type="text" v-model="newItem" @keyup.enter="addNew()" />
    <ul>
      <li v-for="item in items" :class="{'finished' : item.isFinished}" v-on:click="toggleFinish(item)">
        {{item.label}}
      </li>
    </ul>
    <button v-on:click="changeVal">click</button>
  </div>
</template>

<script>
  import store from './components/store'

export default {
  data(){
    return {
      title: 'this is a todo list',
      items: store.fech(),
      newItem : '',
      val: {
        t1: 1,
        t2: 2
      }
    }
  },
  watch: {
    items: {
      handler(items){
        store.save(items);
      },
      deep: true,                  //监听对象内部变化
    },
    val:{
      handler(val){
        console.log(val)
      },
      deep: true,
    },
    'val.t1':{
      handler(val,oldVal){
        console.log(val,oldVal);
      }
    }
  },
  methods:{
    toggleFinish(item){
      item.isFinished = !item.isFinished;
    },
    addNew(){
      if(this.newItem.trim().length) {
        this.items.push({
          label: this.newItem,
          isFinished: false
        });
      }

      this.newItem=''
    },
    changeVal(){
      this.val.t1++;
    }
  }
}
</script>

<style>
  .finished{
    text-decoration: underline;
  }
  html{
    height:100%;
  }
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



